import { Link } from 'react-router-dom'
import { useTranslation } from "react-i18next"

import { useTheme } from 'styled-components'
import styled from 'styled-components/macro'
import styles from './menu.module.scss'

import i18n from 'plugins/i18n'

import SubMenu from './SubMenu'

export interface MenuLiProps {
    children: React.ReactNode
    active: boolean
    width: number
};

const MenuUl = styled.ul`
float: left;
margin-left: 40px;
`

const MenuLi = styled.li<MenuLiProps>`
float: left;
margin-top: 29px;
margin-left: 10px;
padding: 6px 0px;
    
cursor: pointer;
    .menu_txt_a {
        list-style-type: none;
        transition: 200ms;
        width: ${({ width }) => width.toString() + 'px'};
        text-align: center;

        font-family: ${i18n.language === 'zh_CN' ? 'Source Han Sans CN' : 'Roboto'};

        color: ${({ active, theme }) => active ? theme.colors.menuActived : theme.colors.menuInactived};

        &:hover {
            color:  ${({ theme }) => theme.colors.menuActived};
        }

}
`

export interface MenuLiImgProps {
    isShow: boolean
}

const MenuLiImg = styled.img<MenuLiImgProps>`
margin-top: 35px;
display: ${({ isShow }) => isShow ? 'block' : 'none'};
`

const MenuLiDiv = styled.div`
`

export interface MenuProps {
    menu_data: any
    location: any
}

const Menu = ({ menu_data, location }: MenuProps) => {

    const { t } = useTranslation()
    const theme: any = useTheme()

    return (
        <MenuUl>
            {
                menu_data.map((value: any, index: number) => {
                    var isActive = location.pathname == value['link']
                    const hasSubmenu = value['submenus'].length > 0
                    if (!isActive && hasSubmenu) {
                        value['submenus'].map((sub_value: any, sub_index: number) => {
                            if (sub_value['link'] == location.pathname) {
                                isActive = true
                                return
                            }
                        })
                    }
                    return (
                        <MenuLiDiv className={styles.dropdown} key={index}>
                            <MenuLi active={isActive} width={value['width']}>
                                {
                                    hasSubmenu ? <div className={'menu_txt_a'}>{t(value['title'])}</div> : <Link to={value['link']}><div className={'menu_txt_a'}>{t(value['title'])}</div></Link>
                                }
                            </MenuLi>
                            <MenuLiImg isShow={hasSubmenu} src={theme.imgs.submenu_down} />
                            {
                                hasSubmenu ? <SubMenu submenus={value['submenus']} location={location} /> : <></>
                            }
                        </MenuLiDiv>
                    )
                })
            }
        </MenuUl>
    );

}

export default Menu
